.letter-k {
  /* responsive */
  display: block;
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 5;
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  height: 15%;
  -webkit-filter: drop-shadow(0 0 10px rgba(35, 96, 206, 0.27));
          filter: drop-shadow(0 0 10px rgba(35, 96, 206, 0.27)); }

.letter-k-hidden {
  opacity: 0; }

.letter-k-show {
  opacity: 1; }

.hide {
  display: none; }

