/* Playground */
#playground-content {
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 5;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out; }

.playground-hide {
  opacity: 0; }

.playground-show {
  opacity: 1; }



